@use "sass:map";
@import "style/pages/theme";


.header {
    display: flex;
    gap: 20px;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
}

.logo {
    width: 298px;

    height: 63px;
    display: flex;

    .logo-left {
        width: 63px;
        height: 63px;
        border-radius: 63px;
        background: #FF64AE;
        margin: 0 auto;

        .logo-left1 {
            border-radius: 6px;
            width: 13px;
            height: 13px;
            flex-shrink: 0;
            margin-top: 25%;
            margin-left: 20%;

        }

        .logo-left2 {
            border-radius: 6px;
            width: 6px;
            height: 6px;
            flex-shrink: 0;


        }

        .logo-left3 {
            width: 40px;
            height: 11px;
            margin-left: 60%;
            padding-bottom: 30px;

        }

    }

    .logo-right {
        margin-left: 20px;
        margin-top: 20px;
        width: 161px;
        height: 34px;
        color: #091156;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: 2.4px;
    }

}

.nav {
    padding: 10px;
    width: 350px;
    height: 25px;
    line-height: 45px;
    display: flex;
    text-align: center;
    gap: 50px;
    font-size: var(--font);
    color: #8B8B8B;
}

.btn {

    margin-top: 15px;
    width: 100px;
    height: 40.36px;
    margin-left: 200px;
    background-color: #FF64AE;
    color: #ffffff;
    border: none;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    cursor: pointer;
    box-shadow: 0px 12px 17px #FFEDF6;
    border-radius: 50px;

}

@media only screen and (min-width: 40em) {
    .header {
        gap: 10px;
        height: 100%;
        height: auto;
        margin: 0 auto;
        padding: 20px;
       
    }

    .nav {
        padding: 10px;
        width: 100%;
        height: auto;
       
        
        text-align: center;
        font-size: var(--font);
        color: #8B8B8B;
    }

    .btn {

        margin-top: 15px;
        width: 100px;
        height: 40.36px;
        background-color: #FF64AE;
        color: #ffffff;
        border: none;
        text-align: center;
        text-decoration: none;
        font-size: 16px;
        cursor: pointer;
        box-shadow: 0px 12px 17px #FFEDF6;
        border-radius: 50px;
    
    }

    .logo {
        width: 298px;
        height: 63px;
        display: flex;}

}

/* Medium devices such as tablets (768px and up) */
@media only screen and (min-width: 48em) {}